
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
  name: 'todoBody',
  props:{
    tabState:{
      type:Number,
      default:0
    },
    todoList:{
      type:Array,
      default:[]
    },
    completeList:{
      type:Array,
      default:[]
    },
    deleteList:{
      type:Array,
      default:[]
    }
  },
  components:{
    Swipeout, SwipeoutItem, SwipeoutButton
  },
  methods:{
    completeHandle (id) {//id
      let oDate=new Date();
      let obj={
        eventID:id,
        newID:oDate.getTime()
      }
      this.$store.dispatch("todoSetComplete",obj);
    },
    deleteHandle (id) {
      let oDate=new Date();
      let obj={
        eventID:id,
        newID:oDate.getTime()
      }
      this.$store.dispatch("todoSetDelete",obj);
    },
    todoHandle (id) {
      let oDate=new Date();
      let obj={
        eventID:id,
        newID:oDate.getTime()
      }
      this.$store.dispatch("todoSetTodo",obj);
    },
    selContOpen(id){
      this.$store.dispatch("todoSelContShowOpen",id);
    }
  },
  mounted(){

  }
}



